.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.fui-dropzone-root {
  width: 300px !important;
  height: 300px;
  margin: 10px;
  left: 25%;
}

.files-ui-dropzone-extra-default {
    border: 1px dashed #1e63ab !important;
    background: #f5f8fb;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.box-wrapper {
  height: 95vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.box-wrapper {
  height: 95vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-family: Helvetica Neue;
}

#box1 {
  padding: 10px;
  background: #1e63ab;
  color: white;
  text-align: left;
  font-size: x-large;
}

#box2 {
  padding: 8px;
}

#box3 {
  padding: 10px;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
}

#box4 {
  width:50%;
  line-height:1.4;
}
p {
  text-align: justify;
}

.middle-column {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.middle-column div+div {
  margin-top: 8px
}

#box8 {
  flex-grow: 1;
}

#box5 {
  width: 50%
}
label {
  font-family: auto;
}
.upload {
  padding: 5px;
  margin-right:5px;
  height:40px;
  background-color: #1e63ab;
  color: white;
  box-shadow: 0 5px 6px 0 rgb(0 0 0 / 15%);
  border: 2px solid #1e63ab; 
  cursor: pointer;
  font-weight: bold;
}

.upload:hover {
  background-color: white;
  color: black;
}
.download {
  margin: 5px;
}